<template>
  <el-container class="newjobDescription" id="content">
    <el-header>
      <el-row type="flex" class="row-bg" justify="end" align="middle">
        <el-input placeholder="请输入内容" v-model="flowName">
          <template slot="prepend">流程名称</template>
        </el-input>
        <el-button type="info" id="designSet" @click="resetJsplumbChart">
          <i class="icon iconfont icon-ir-set"></i>&nbsp;重置
        </el-button>
        <el-button type="info" id="drawReturn" @click="goBack">
          <i class="icon iconfont icon-ir-left"></i>&nbsp;返回
        </el-button>

        <el-button type="info" id="designSave" @click="saveFlowData" :disabled="!this.flowName">
          <i class="icon iconfont icon-ir-save"></i>&nbsp;保存
        </el-button>
        <el-button type="info" id="designSave" @click="getConfigData" :disabled="!this.flowName">
          <i class="icon iconfont icon-ir-ok"></i>&nbsp;提交
        </el-button>
        <!-- <el-button type="info" id="designSave" @click="startFlow">
          <i class="icon iconfont icon-ir-add"></i>&nbsp;调度
        </el-button>-->
        <el-button type="info" id="designDelSave" @click="reset">
          <i class="fa fa-eraser"></i>&nbsp;清空
        </el-button>
        <el-button type="info" id="designDelSave" @click="Screen">
          <i class="fa fa-eraser"></i>&nbsp;全屏
        </el-button>
      </el-row>
    </el-header>
    <el-container>
      <el-aside width="220px">
        <leftaside :stepList="stepList"></leftaside>
      </el-aside>
      <el-container>
        <el-main class="jsplumb-editor">
          <drop class="drop-workplace" @drop="handleDrop" id="workplace">
            <!-- <jsplumbchart
              :data="{
                steps: flowData,
                links: links,
                container: 'workplace',
                nodeType:'flowchartnode',
                jsPlumb: jsPlumb
              }"
              @modifyChart="modifyChart"
              @nodedblClick="nodedblClick"
              @handleDrop="handleDrop"
              ref="jsplumbchart"
            ></jsplumbchart>-->

            <jsplumbchart
              :data="jsplumbchartOption"
              @modifyChart="modifyChart"
              @nodedblClick="nodedblClick"
              @handleDrop="handleDrop"
              ref="jsplumbchart"
            ></jsplumbchart>
          </drop>
        </el-main>
      </el-container>
    </el-container>

    <stepdialog :data="dialogOption" @modifyFlowData="modifyFlowData"></stepdialog>

    <!-- <cepConfigDialog :data="configCepData"></cepConfigDialog> -->
    <runtimeSettingsDialog
      :data="runtimeSettingsdialogOption"
      @setRuntimeSettingsdialogOption="setRuntimeSettingsdialogOption"
    ></runtimeSettingsDialog>

    <el-dialog
      title="数据集查询"
      :visible.sync="realtime.stepDialogSate"
      :before-close="cancelDataSetDialog"
      custom-class="dialogClass"
      :close-on-click-modal="false"
      :modal-append-to-body="false"
      v-dialog-drag
    >
      <el-form>
        <data-source-inquire
          ref="dataSourceInquire"
          @thisSelectDatasetVal="getSelectDateVal"
          rootName="dataset_dir"
        ></data-source-inquire>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="selectDataSet" name="bm-ch-step2Form-dataset-ok">确定</el-button>
        <el-button @click="cancelDataSetDialog" name="bm-ch-step2Form-dataset-cancel">取消</el-button>
      </div>
    </el-dialog>

    <el-dialog
      title="CEP 设置"
      :visible.sync="realtime.dialogCepFormVisibleStatus"
      :before-close="cancelCepDialog"
      :close-on-click-modal="false"
      :modal-append-to-body="false"
      v-dialog-drag
      width="97%"
      min-height="900px"
      custom-class="dialogClass-cep"
      @opened="openedDialog"
    >
      <cepConfigDialog :data="configCepData"></cepConfigDialog>
      <div slot="footer" class="dialog-footer-cep">
        <el-button type="primary" @click="setCepData" name="bm-ch-step2Form-dataset-ok">确定</el-button>
        <el-button @click="cancelCepDialog" name="bm-ch-step2Form-dataset-cancel">取消</el-button>
      </div>
    </el-dialog>
  </el-container>
</template>

<script>
import _ from "lodash";
import jsplumbchart from "@/components/jsplumbchart/index";
import leftaside from "@/components/aside/left/index";
import { mapGetters, mapActions, mapState } from "vuex";

import dataSourceInquire from "@dataSourceInq/dataSourceInquire.vue";
import { steplist, stepsettings, modifyflow } from "@server/realtime/index";

import stepdialog from "../set/StepDialog/index";
import cepConfigDialog from "../set/tab/parameter/common/CepConfigDialog/index";

import runtimeSettingsDialog from "../set/runtimeSettings/index";
import * as jsPlumb from "@static/jsPlumb/jsPlumb-2.2.3-min";

import {
  createflow,
  queryflow,
  creatscheduler,
  runtimeSettings
} from "@/api/realtime/index.js";

//drawJsplumbChart addEndpointToNode connect

//test data
// import { flowData } from "@mock/data/flowData.js";
export default {
  watch: {
    "realtime.stepDialogDataOption"(val) {
      this.dialogOption = val;
    },
    "realtime.dialogCepFormVisibleStatus"(val) {
      this.dialogCepFormVisible = val;
    },
    dialogCepFormVisible(val) {
      this.configCepData = { dialogCepFormVisible: val };
    }
  },
  components: {
    leftaside,
    stepdialog,
    dataSourceInquire,
    runtimeSettingsDialog,
    jsplumbchart,
    cepConfigDialog
  },
  data: function() {
    return {
      configCepData: { dialogCepFormVisible: false },
      dialogCepFormVisible: false,
      currentFlowObject: "",
      runtimeSettingsdialogOption: {},
      dialogOption: {},
      activeName: "second",
      flowType: "",
      flowData: [],
      links: [],
      self: this,
      dialogVisible: false,
      dialogComponent: "",
      nodeType: "",
      toolkit: "",
      stepList: [],
      dialogVisible: false,
      nodeTab: [
        {
          title: "输入",
          name: "input",
          icon: "fa fa-sign-in",
          lable: "input"
        },
        {
          title: "参数",
          name: "parameter",
          icon: "fa fa-gear",
          lable: "parameter"
        },
        {
          title: "输出",
          name: "output",
          icon: "fa fa-sign-out",
          lable: "output"
        }
      ],
      selectCheckedItem: {},
      flowName: "",
      jsPlumb: jsPlumb,
      jsplumbchartOption: {
        steps: this.flowData,
        links: this.links,
        container: "workplace",
        nodeType: "flowchartnode",
        jsPlumb: jsPlumb
      }
      // getInstance:getInstance
    };
  },
  computed: {
    ...mapState(["realtime"])
  },
  mounted() {
    this.initData();
  },
  beforeCreate() {},
  created() {},
  beforeMount() {},
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed: function() {},
  methods: {
    ...mapActions([
      "openDialog",
      "setOutData",
      "setDataSetSelect",
      "setCurrentFlowData",
      "initInputCheckedData",
      "initOutInputData",
      "modifyLoadingStatus",
      "initParametData",
      "initOutCheckedData",
      "initInputData",
      "setAdList",
      "setStepDailogDataOption",
      "setCepConfigDailog",
      "setParamertData",
      "FLOW_CEP_DATA_ACTION"
    ]),
    clearAllStatus() {
      this.setDataSetSelect({});
      this.initParametData({});
      this.initOutCheckedData([]);
      this.initInputCheckedData([]);
      this.initInputData([]);
      this.modifyLoadingStatus(false);
      this.setAdList([]);
    },
    resetJsplumbChart() {
      this.$confirm("确定进行重置操作?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          // this.$message({
          //   type: "success",
          //   message: "删除成功!"
          // });
          this.$refs.jsplumbchart.resetJsplumbChart();
          this.flowData = this.currentFlowObject.steps;
          this.links = this.currentFlowObject.links;
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    startFlow() {
      if (!this.currentFlowObject.id) {
        this.$message({
          message: "请保存当前流程!",
          type: "warning"
        });
        return;
      }
      if (!this.runtimeSettingsdialogOption.itemList) {
        this.$message({
          message: "请提交当前流程!",
          type: "warning"
        });
        return;
      }
      let data = {
        flowId: this.currentFlowObject.id,
        flowName: this.currentFlowObject.name,
        createTime: new Date().getTime(),
        name: this.runtimeSettingsdialogOption.name,
        configurations: {
          arguments: [
            {
              name: "string",
              value: {}
            }
          ],
          properties: this.runtimeSettingsdialogOption.itemList,
          extraConfigurations: [
            {
              name: "string",
              value: {}
            }
          ]
        }
      };
      this.scheduler(data);
    },
    scheduler(data) {
      creatscheduler(data).then(
        data => {
          if (data.status == 200) {
            this.$message({
              message: "操作成功",
              type: "success"
            });
          }
        },
        err => {
          this.$message({
            message: err.response.message,
            type: "error",
            duration: 1500
          });
        }
      );
    },
    getLinksData() {
      this.links = filterLinkData(
        _.map(this.jsplumbInstance.getAllConnections(), item => {
          return {
            name: item.id,
            source: item.sourceId,
            sourceOutput: item.endpoints[0].canvas.nextSibling.textContent,
            target: item.targetId,
            targetInput: item.target.dataset.type,
            input: item.endpoints[1].canvas.nextSibling.textContent
          };
        }),
        _
      );
    },
    getSelectDateVal(data) {
      this.selectCheckedItem = data;
    },
    selectDataSet() {
      this.setDataSetSelect(this.selectCheckedItem);
      this.cancelDataSetDialog();
    },
    cancelDataSetDialog() {
      this.$refs.dataSourceInquire.clearSelectionRow();
      this.openDialog(false);
    },
    cancelCepDialog() {
      this.setCepConfigDailog(false);
      //this.setCepFlowData({});
    },
    setCepData() {
      let flowCepData = this.realtime.FLOW_CEP_DATA_STATA;
      if (_.isEmpty(flowCepData.name)) {
        this.$message({
          message: "名称不可以为空!",
          type: "warning"
        });
        return;
      }
      this.setCepConfigDailog(false);

      this.setParamertData({
        ...this.realtime.parametData,
        cepRule: flowCepData
      });
    },
    openedDialog() {
      //console.log("this.realtime.parametData", this.realtime.parametData);

      let cepRule = this.realtime.parametData.cepRule;

      if (cepRule) {
        this.FLOW_CEP_DATA_ACTION(cepRule);
      }
    },
    handleClick() {},
    resetData() {
      this.initData();
    },
    goBack() {
      this.$router.go(-1);
    },
    getConfigData() {
      this.runtimeSettingsdialogOption = {
        dialogVisible: true,
        flowName: this.flowName
      };
    },
    setRuntimeSettingsdialogOption(val) {
      this.runtimeSettingsdialogOption = val;
      this.scheduler({
        flowId: this.currentFlowObject.id,
        flowName: this.currentFlowObject.name,
        createTime: new Date().getTime(),
        name: this.runtimeSettingsdialogOption.name,
        configurations: {
          arguments: [
            {
              name: "string",
              value: {}
            }
          ],
          // properties: this.runtimeSettingsdialogOption.itemList,
          settings: this.runtimeSettingsdialogOption.itemList,
          extraConfigurations: [
            {
              name: "string",
              value: {}
            }
          ]
        }
      });
    },
    saveFlowData() {
      // console.log(this.flowData);
      // console.log(this.links);
      // return;

      if (!this.flowName) {
        this.$message({
          message: "请填写流程名称",
          type: "warning"
        });
        return;
      }

      let data = {
        id: this.$route.query.id,
        name: this.flowName,
        createTime: new Date().getTime(),
        lastModifyTime: new Date().getTime(),
        steps: this.flowData,
        links: this.links
      };

      // console.log("data",data);

      // console.log(" this.flowData", this.flowData);
      // return;

      if (this.$route.query.id) {
        modifyflow(data).then(
          data => {
            if (data.status == 200) {
              this.currentFlowObject = data.data;
              this.$message({
                message: "操作成功",
                type: "success"
              });
            }
          },
          err => {
            this.$message({
              message: err.response.message,
              type: "error",
              duration: 1500
            });
          }
        );
        return;
      }

      createflow(data).then(
        data => {
          if (data.status == 200) {
            this.currentFlowObject = data.data;
            // this.flowName = "";
            //this.reset();
            this.$message({
              message: "操作成功",
              type: "success"
            });
            //this.goBack();
          }
        },
        err => {
          this.$message({
            message: err.response.message,
            type: "error",
            duration: 1500
          });
        }
      );
    },
    modifyFlowData(val) {
      // console.log("this.flowData ", this.flowData);
      // console.log(val);
      this.flowData = _.map(this.flowData, item => {
        if (
          item.type == val.step.type &&
          val.step.type == "source" &&
          item.id == val.step.id
        ) {
          return {
            ...item,
            stepSettings: val.data.parametData,
            outputConfigurations: val.data.checkedOutData
            // outputConfigurations: {
            //   output: val.data.checkedOutData
            // }
          };
        }

        if (
          item.type == val.step.type &&
          (val.step.type != "source" || val.step.type != "sink") &&
          item.id == val.step.id
        ) {
          return {
            ...item,
            stepSettings: val.data.parametData,
            outputConfigurations: val.data.checkedOutData,
            // outputConfigurations: {
            //   output: val.data.checkedOutData
            // },
            inputConfigurations: val.data.checkedinPUTData
            // inputConfigurations: {
            //   input: val.data.checkedinPUTData
            // }
          };
        }

        // if (
        //   item.type == val.step.type &&
        //   (val.step.type == "filter" ||
        //     val.step.type == "aggregate" ||
        //     val.step.type == "transform" ||
        //     val.step.type == "source_dummy" ||
        //     val.step.type == "join" ||
        //     val.step.type == "sql") &&
        //   item.id == val.step.id
        // ) {
        //   return {
        //     ...item,
        //     stepSettings: val.data.parametData,
        //     outputConfigurations: {
        //       output: val.data.checkedOutData
        //     },
        //     inputConfigurations: {
        //       input: val.data.checkedinPUTData
        //     }
        //   };
        // }

        if (
          item.type == val.step.type &&
          val.step.type == "sink" &&
          item.id == val.step.id
        ) {
          return {
            ...item,
            stepSettings: val.data.parametData,
            inputConfigurations: val.data.checkedinPUTData
            // inputConfigurations: {
            //   input: val.data.checkedinPUTData
            // }
          };
        }
        return item;
      });

      this.jsplumbchartOption = {
        ...this.jsplumbchartOption,
        steps: this.flowData,
        links: this.links
      };
    },
    // modifyFlowData(val) {
    //   this.flowData = _.map(this.flowData, item => {
    //     if (
    //       item.type == val.type &&
    //       val.type == "source" &&
    //       item.id == val.id
    //     ) {
    //       console.log('  val.type == "source" ');
    //       console.log("item", item);
    //       console.log("current step", val);
    //       return {
    //         ...item,
    //         stepSettings: this.realtime.parametData,
    //         outputConfigurations: {
    //           output: this.realtime.checkedOutData
    //         }
    //       };
    //     }

    //     if (
    //       item.type == val.type &&
    //       (val.type == "filter" ||
    //         val.type == "aggregate" ||
    //         val.type == "transform" ||
    //         val.type == "sql") &&
    //       item.id == val.id
    //     ) {
    //       console.log('val.type == "filter"');
    //       console.log("item", item);
    //       console.log("current step", val);
    //       return {
    //         ...item,
    //         stepSettings: this.realtime.parametData,
    //         outputConfigurations: {
    //           output: this.realtime.checkedOutData
    //         },
    //         inputConfigurations: {
    //           input: this.realtime.checkedinPUTData
    //         }
    //       };
    //     }

    //     if (item.type == val.type && val.type == "sink" && item.id == val.id) {
    //       console.log('val.type == "sink"');
    //       console.log("item", item);
    //       console.log("current step", val);
    //       return {
    //         ...item,
    //         stepSettings: this.realtime.parametData,
    //         inputConfigurations: {
    //           input: this.realtime.checkedinPUTData
    //         }
    //       };
    //     }

    //     console.log("else item", item);

    //     return item;
    //   });

    //   console.log("this.flowData", this.flowData);
    // },

    initData() {
      // this.stepList = [
      //   {
      //     id: "rtc_source",
      //     name: "rtc_source",
      //     type: "source",
      //     tags: ["IO", "rtcflow"],
      //     stepSettings: {},
      //     outputConfigurations: {
      //       output: [{ column: "", type: "", alias: "", description: "" }]
      //     }
      //   },
      //   {
      //     id: "rtc_filter",
      //     name: "rtc_filter",
      //     type: "filter",
      //     tags: ["Transform", "rtcflow"],
      //     stepSettings: {},
      //     inputConfigurations: {
      //       input: [{ column: "", type: "", alias: "", description: "" }]
      //     },
      //     outputConfigurations: {
      //       output: [{ column: "", type: "", alias: "", description: "" }]
      //     }
      //   },
      //   {
      //     id: "rtc_aggregate",
      //     name: "rtc_aggregate",
      //     type: "aggregate",
      //     tags: ["Transform", "rtcflow"],
      //     stepSettings: {},
      //     inputConfigurations: {
      //       input: [{ column: "", type: "", alias: "", description: "" }]
      //     },
      //     outputConfigurations: {
      //       output: [{ column: "", type: "", alias: "", description: "" }]
      //     }
      //   },
      //   {
      //     id: "rtc_transform",
      //     name: "rtc_transform",
      //     type: "transform",
      //     tags: ["Transform", "rtcflow"],
      //     stepSettings: {},
      //     inputConfigurations: {
      //       input: [{ column: "", type: "", alias: "", description: "" }]
      //     },
      //     outputConfigurations: {
      //       output: [{ column: "", type: "", alias: "", description: "" }]
      //     }
      //   },
      //   {
      //     id: "rtc_sql",
      //     name: "rtc_sql",
      //     type: "sql",
      //     tags: ["Transform", "rtcflow"],
      //     stepSettings: {},
      //     inputConfigurations: {
      //       input: [{ column: "", type: "", alias: "", description: "" }]
      //     },
      //     outputConfigurations: {
      //       output: [{ column: "", type: "", alias: "", description: "" }]
      //     }
      //   },
      //   {
      //     id: "rtc_sink",
      //     name: "rtc_sink",
      //     type: "sink",
      //     tags: ["IO", "rtcflow"],
      //     stepSettings: {},
      //     inputConfigurations: {
      //       input: [{ column: "", type: "", alias: "", description: "" }]
      //     }
      //   }
      // ];

      this.stepList = this.getLeftAsideData(
        this.stepList,
        _.uniq(this.getGroupData(this.stepList))
      );
      //matrix(1, 0, 0, 1, 0, 0)
      //resetJsplumbChart
      //this.$refs.jsplumbchart.resetJsplumbChart();
      steplist().then(
        data => {
          // this.stepList=data;
          // this.getGroupData(data);
          // console.log(JSON.stringify(data));
          this.stepList = this.getLeftAsideData(
            data,
            _.uniq(this.getGroupData(data))
          );
          // console.log("父组件");
          // console.log(JSON.stringify(this.stepList));
        },
        err => {
          // this.$message({
          //   message: err.response.message,
          //   type: "error",
          //   duration: 1500
          // });
        }
      );

      // console.log("测试数据", flowData);
      let data = {
        id: "402881d26cb71fd1016cb78a9d8c0004",
        name: "flow_2",
        createTime: 1566525170482,
        lastModifyTime: 1566525170482,
        steps: [
          {
            id: "rtc_source_1",
            name: "rtc_source",
            type: "source",
            stepSettings: {},
            outputConfigurations: { output: [] },
            x: 87,
            y: 120
          },
          {
            id: "rtc_sink_2",
            name: "rtc_sink",
            type: "sink",
            stepSettings: {
              quoteChar: "风格",
              path: "风格",
              serialVersionUID: "的鬼地方鬼地方"
            },
            inputConfigurations: { input: [] },
            x: 1265,
            y: 45
          },
          {
            id: "rtc_filter_3",
            name: "rtc_filter",
            type: "filter",
            stepSettings: {},
            inputConfigurations: { input: [] },
            outputConfigurations: { output: [] },
            x: 557,
            y: 9
          }
        ],
        links: [
          {
            name: "con_18",
            source: "rtc_source_1",
            sourceOutput: "output",
            target: "rtc_sink_2",
            targetInput: "input",
            input: "input"
          }
        ]
      };
      // this.flowData = data.steps;
      // this.links = data.links;
      // return;

      if (this.$route.query.id) {
        queryflow(this.$route.query.id).then(
          data => {
            if (data.status == 200) {
              let result = data.data;
              this.flowName = result.name;
              this.flowData = result.steps ? result.steps : [];
              this.links = result.links ? result.links : [];
              this.currentFlowObject = result;
              this.jsplumbchartOption = {
                ...this.jsplumbchartOption,
                steps: this.flowData,
                links: this.links
              };
            }
          },
          err => {
            // this.$message({
            //   message: err.response.message,
            //   type: "error",
            //   duration: 1500
            // });
          }
        );
      }
    },
    reset() {
      this.$confirm("确定进行清空操作?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          // this.$message({
          //   type: "success",
          //   message: "删除成功!"
          // });
          this.flowData = [];
          this.links = [];
          this.$refs.jsplumbchart.reset();
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    copyNode(node) {
      let uuid = jsPlumbUtil.uuid();
      return {
        ...node,
        id: this.isExitStepID(node.id) ? node.id + "_new" + uuid : node.id
      };
    },
    handleDrop(val) {
      this.flowData.push(
        val.drawIcon ? this.getCurrentNode(val) : this.copyNode(val)
      );
    },
    isExitStepID(val) {
      let result = false;
      _.forEach(this.flowData, item => {
        if (item.id == val) {
          result = true;
        }
      });

      return result;
    },
    getCurrentNode(data) {
      let uuid = jsPlumbUtil.uuid();
      let stepId = data.drawIcon.id + "_" + (this.flowData.length + 1);
      let newstepid = this.isExitStepID(stepId)
        ? stepId + "_new" + uuid
        : stepId;
      let node = {
        // id: data.drawIcon.id + "_" + (this.flowData.length + +1),
        id: this.isExitStepID(newstepid) ? newstepid + "_new" : newstepid,
        name: data.drawIcon.name,
        type: data.drawIcon.type,
        x: event.offsetX,
        y: event.offsetY,
        stepSettings: data.drawIcon.stepSettings
      };

      let outputConfigurations = {
        outputConfigurations: {
          output: []
        }
      };

      let inputConfigurations = {
        inputConfigurations: {
          input: []
        }
      };

      switch (data.drawIcon.type) {
        case "source":
        case "source_dummy":
          return {
            ...node,
            ...outputConfigurations
          };
        // case "source_dummy":
        //   return {
        //     ...node,
        //     ...outputConfigurations
        //   };
        // case "filter":
        //   return {
        //     ...node,
        //     ...inputConfigurations,
        //     ...outputConfigurations
        //   };
        // case "join":
        //   return {
        //     ...node,
        //     ...inputConfigurations,
        //     ...outputConfigurations
        //   };
        // case "lookup":
        //   return {
        //     ...node,
        //     ...inputConfigurations,
        //     ...outputConfigurations
        //   };

        // case "aggregate":
        //   return {
        //     ...node,
        //     ...inputConfigurations,
        //     ...outputConfigurations
        //   };
        // case "transform":
        //   return {
        //     ...node,
        //     ...inputConfigurations,
        //     ...outputConfigurations
        //   };
        // case "sql":
        //   return {
        //     ...node,
        //     ...inputConfigurations,
        //     ...outputConfigurations
        //   };
        case "sink":
          return {
            ...node,
            ...inputConfigurations
          };
        default:
          return {
            ...node,
            ...inputConfigurations,
            ...outputConfigurations
          };
      }
    },
    findLinks(val) {
      return _.find(this.links, function(item) {
        return item.target == val.id;
      });
    },
    filterLinks(val) {
      return _.filter(this.links, function(item) {
        return item.target == val.id;
      });
    },
    getLinks(val) {
      return _.filter(this.links, function(item) {
        return item.target == val.id;
      });
    },
    isOpenStepDialog(val) {
      if (val.type == "source" || val.type == "source_dummy") {
        return false;
      }
      //console.log("this.findLinks(val)", this.findLinks(val));
      // if (
      //   ((val.type == "filter" ||
      //     val.type == "sink" ||
      //     val.type == "aggregate" ||
      //     val.type == "transform" ||
      //     // val.type == "join" ||
      //     val.type == "lookup" ||
      //     val.type == "split" ||
      //     val.type == "sql") &&
      //     this.links.length == 0) ||
      //   ((val.type == "filter" ||
      //     val.type == "sink" ||
      //     val.type == "aggregate" ||
      //     val.type == "transform" ||
      //     // val.type == "join" ||
      //     val.type == "lookup" ||
      //     val.type == "split" ||
      //     val.type == "sql") &&
      //     !this.findLinks(val))
      // ) {
      //   return true;
      // }

      if (
        val.type == "join" &&
        (this.filterLinks(val).length == 0 || this.filterLinks(val).length == 1)
      ) {
        return true;
      }

      if (
        (val.type == "filter" ||
          val.type == "sink" ||
          val.type == "aggregate" ||
          val.type == "transform" ||
          // val.type == "join" ||
          val.type == "lookup" ||
          val.type == "split" ||
          val.type == "sql" ||
          val.type == "cep") &&
        !this.filterLinks(val)[0]
      ) {
        return true;
      }
    },

    getPreNodes(links) {
      let result = [];
      _.forEach(links, p => {
        _.forEach(this.flowData, s => {
          if (p.source == s.id) {
            result.push(s);
          }
        });
      });
      return result;
    },

    getPreNode(val) {
      return _.find(this.flowData, function(o) {
        if (!val) {
          return "";
        }
        return o.id == val.source;
      });
    },

    nodedblClick(val) {
      // console.log("this.findLinks(val)",this.findLinks(val));
      // console.log(this.getPreNode(this.findLinks(val)));
      if (this.isOpenStepDialog(val)) {
        this.$message({
          message: "请建立正确的连接！",
          type: "error"
        });
        return;
      }

      //nodeSetTab
      // this.dialogOption = {
      //   dialogVisible: true,
      //   step: val,
      //   filterLinks: this.filterLinks(val),
      //   preSteps: this.getPreNodes(this.filterLinks(val)),
      //   pre: this.getPreNode(this.findLinks(val)),
      //   nodeSetTab: this.setNodeSetTab(
      //     val,
      //     this.getPreNode(this.findLinks(val))
      //   ),
      //   flowData: this.flowData,
      //   links: this.links
      // };
      this.setStepDailogDataOption({
        dialogVisible: true,
        step: val,
        filterLinks: this.filterLinks(val),
        preSteps: this.getPreNodes(this.filterLinks(val)),
        pre: this.getPreNode(this.findLinks(val)),
        nodeSetTab: this.setNodeSetTab(
          val,
          this.getPreNode(this.findLinks(val))
        ),
        flowData: this.flowData,
        links: this.links
      });
      //this.modifyLoadingStatus(true);
    },
    setNodeSetTab(val, pre) {
      if (val.type == "sink") {
        return [
          {
            ...this.nodeTab[0],
            title: "输入 (" + pre.name + ")"
          },
          this.nodeTab[1]
        ];
      }
      if (val.type == "source" || val.type == "source_dummy") {
        return [this.nodeTab[1], this.nodeTab[2]];
      }
      if (
        val.type == "filter" ||
        val.type == "aggregate" ||
        val.type == "sql" ||
        val.type == "transform" ||
        val.type == "lookup" ||
        val.type == "split" ||
        val.type == "cep"
      ) {
        return [
          {
            ...this.nodeTab[0],
            title: "输入 (" + pre.name + ")"
          },
          this.nodeTab[1],
          this.nodeTab[2]
        ];
      }

      if (val.type == "join") {
        // console.log("this.getLinks(val)", this.getLinks(val));
        let result = [];
        _.forEach(this.getLinks(val), element => {
          result.push({
            ...this.nodeTab[0],
            title:
              element.targetInput + " (" + this.getPreNode(element).name + ")",
            targetInput: element.targetInput
          });
        });
        return [...result, this.nodeTab[1], this.nodeTab[2]];
      }
    },
    Screen() {
      this.fullScreen("content");
    },
    getGroupData(val) {
      return _.map(val, item => {
        return item.tags[0];
      });
    },
    getLeftAsideData(data, group) {
      return _.map(group, item => {
        return {
          group: item,
          thisIcon: _.compact(this.getIconData(data, item))
        };
      });
    },
    getIconData(data, val) {
      return _.map(data, item => {
        if (item.tags[0] == val) {
          return item;
        }
      });
    },
    handleClose(done) {
      this.$confirm("确认关闭？")
        .then(_ => {
          done();
        })
        .catch(_ => {});
    },
    modifyChart(val) {
      this.flowData = val.stepData;
      this.links = val.links;
    }
  }
};
</script>

